////////////////////////////////////////////////////////////////////////////////////////////////////
// JSON format verifiers
////////////////////////////////////////////////////////////////////////////////////////////////////
export const hasValidMetadata = (node) =>
{
	return typeof node.metadata !== "undefined";
};

export const hasValidAction = (node) =>
{
	return typeof node.metadata.action !== "undefined";
};

export const hasValidAnimation = (node) =>
{
	return typeof node.metadata.animation !== "undefined";
};

export const hasValidGeometry = (node) =>
{
	return typeof node.metadata.geometry !== "undefined";
};

export const hasValidIcon = (node) =>
{
	return typeof node.metadata.icon !== "undefined" && node.metadata.icon.name != "";
};

export const hasValidInteractionIndices = (node) =>
{
	return typeof node.metadata.interactionIndices !== "undefined";
};

export const hasValidMediaIndex = (node) =>
{
	return typeof node.metadata.mediaIndex !== "undefined" && node.metadata.mediaIndex >= 0;
};

export const hasValidOverlayIndices = (node) =>
{
	return typeof node.metadata.overlayIndices !== "undefined";
};

export const hasValidText = (node) =>
{
	return typeof node.metadata.text !== "undefined" && typeof node.metadata.text.text !== "undefined" &&
		node.metadata.text.text != "";
};

export const hasValidSvg = (node) =>
{
	return typeof node.metadata.svg !== "undefined";
};

export const hasValidView = (node) =>
{
	return typeof node.metadata.view !== "undefined";
};

export const hasValidVideoStartTime = (node) =>
{
	return typeof node.metadata.videoStartTime !== "undefined" && node.metadata.videoStartTime >= 0;
};

export const hasValidVideoStartTimeOfParent = (node) =>
{
	return typeof node.metadata.videoStartTimeOfParent !== "undefined" && node.metadata.videoStartTimeOfParent >= 0;
};

////////////////////////////////////////////////////////////////////////////////////////////////////
// JSON coordinate adjustments based on window dimension
////////////////////////////////////////////////////////////////////////////////////////////////////
const recomputeGeometry = (element, screenWidth, screenHeight, relative, relativeCoordinateList, scaleX, scaleY,
	invertX, invertY, invertW, invertH) =>
{
	if(element.metadata.geometry.type == "ellipse")
	{
		let positionX = element.metadata.geometry.position.x;
		let positionY = element.metadata.geometry.position.y;
		let radiusX = element.metadata.geometry.radius.x;
		let radiusY = element.metadata.geometry.radius.y;

		if(relative || relativeCoordinateList.includes("geometry.position.x")) positionX *= scaleX;
		else if(invertX) positionX = screenWidth - positionX;
		if(relative || relativeCoordinateList.includes("geometry.position.y")) positionY *= scaleY;
		else if(invertY) positionY = screenHeight - positionY;
		if(relative || relativeCoordinateList.includes("geometry.radius.x")) radiusX *= scaleX;
		else if(invertW) radiusX = screenWidth - radiusX;
		if(relative || relativeCoordinateList.includes("geometry.radius.y")) radiusY *= scaleY;
		else if(invertH) radiusY = screenHeight - radiusY;

		return {
			...element.metadata.geometry,
			position:
			{
				x: positionX,
				y: positionY
			},
			radius:
			{
				x: radiusX,
				y: radiusY
			}
		};
	}
	else if(element.metadata.geometry.type == "rectangle" || element.metadata.geometry.type == "arrow" ||
		element.metadata.geometry.type == "triangle" || element.metadata.geometry.type == "hexagon" ||
		element.metadata.geometry.type == "speech_bubble_ellipse_left" || element.metadata.geometry.type ==
		"speech_bubble_rectangle_left")
	{
		let positionX = element.metadata.geometry.position.x;
		let positionY = element.metadata.geometry.position.y;
		let width = element.metadata.geometry.width;
		let height = element.metadata.geometry.height;

		if(relative || relativeCoordinateList.includes("geometry.position.x")) positionX *= scaleX;
		else if(invertX) positionX = screenWidth - positionX;
		if(relative || relativeCoordinateList.includes("geometry.position.y")) positionY *= scaleY;
		else if(invertY) positionY = screenHeight - positionY;
		if(relative || relativeCoordinateList.includes("geometry.width")) width *= scaleX;
		else if(invertW) width = screenWidth - width;
		if(relative || relativeCoordinateList.includes("geometry.height")) height *= scaleY;
		else if(invertH) height = screenHeight - height;

		return {
			...element.metadata.geometry,
			position:
			{
				x: positionX,
				y: positionY
			},
			width: width,
			height: height
		};
	}
	else if(element.metadata.geometry.type == "floodfill")
	{
	}

	return null;
};

export const recomputeCoordinates = (visionData, screenWidth, screenHeight) =>
{
	if(!hasValidMetadata(visionData)) return visionData;

	const width = visionData.metadata.width;
	const height = visionData.metadata.height;
	if(width <= 0 || height <= 0) return visionData;

	const scaleX = 1.0 / width * screenWidth;
	const scaleY = 1.0 / height * screenHeight;

	// Handle media
	let media = visionData.media;
	for(let i = 0; i < visionData.media.length; ++i)
	{
		if(typeof media[i].width !== "undefined") media[i].width *= scaleX;
		if(typeof media[i].height !== "undefined") media[i].height *= scaleY;
	}

	// Handle overlays
	let overlays = visionData.overlays;
	for(let i = 0; i < visionData.overlays.length; ++i)
	{
		let overlay = visionData.overlays[i];
		if(!hasValidMetadata(overlay)) continue;

		const invertX = typeof overlay.metadata.invert !== "undefined" && (overlay.metadata.invert == "x" ||
			overlay.metadata.invert == "xy" || overlay.metadata.invert == "yx");
		const invertY = typeof overlay.metadata.invert !== "undefined" && (overlay.metadata.invert == "y" ||
			overlay.metadata.invert == "xy" || overlay.metadata.invert == "yx");
		const invertW = typeof overlay.metadata.invertSize !== "undefined" && (overlay.metadata.invertSize == "w" ||
			overlay.metadata.invertSize == "wh" || overlay.metadata.invertSize == "hw");
		const invertH = typeof overlay.metadata.invertSize !== "undefined" && (overlay.metadata.invertSize == "h" ||
			overlay.metadata.invertSize == "wh" || overlay.metadata.invertSize == "hw");
		const relative = typeof overlay.metadata.relativeCoordinateSystem !== "undefined";
		const relativeCoordinateList = typeof overlay.metadata.mixedCoordinateSystem !== "undefined" ?
			overlay.metadata.mixedCoordinateSystem : [];

		const geometry = recomputeGeometry(overlay, screenWidth, screenHeight, relative, relativeCoordinateList,
			scaleX, scaleY, invertX, invertY, invertW, invertH);

		if(hasValidSvg(overlay) && overlay.metadata.geometry.type == "arrow")
		{
			let arrowBody = overlay.metadata.svg.arrowBody;
			let arrowHeadLeft = overlay.metadata.svg.arrowHeadLeft;
			let arrowHeadRight = overlay.metadata.svg.arrowHeadRight;
			let strokeWidth = overlay.metadata.svg.strokeWidth;

			const isArrowBodyX1Relative = relative || relativeCoordinateList.includes("svg.arrowBody.x1");
			const isArrowBodyY1Relative = relative || relativeCoordinateList.includes("svg.arrowBody.y1");
			const isArrowBodyX2Relative = relative || relativeCoordinateList.includes("svg.arrowBody.x2");
			const isArrowBodyY2Relative = relative || relativeCoordinateList.includes("svg.arrowBody.y2");

			if(isArrowBodyX1Relative) arrowBody.x1 *= scaleX;
//			else if(invertX) arrowBody.x1 = screenWidth - arrowBody.x1;
			if(isArrowBodyY1Relative) arrowBody.y1 *= scaleY;
//			else if(invertY) arrowBody.y1 = screenHeight - arrowBody.y1;
			if(isArrowBodyX2Relative) arrowBody.x2 *= scaleX;
//			else if(invertX) arrowBody.x2 = screenWidth - arrowBody.x2;
			if(isArrowBodyY2Relative) arrowBody.y2 *= scaleY;
//			else if(invertY) arrowBody.y2 = screenHeight - arrowBody.y2;

			const isArrowHeadLeftX1Relative = relative || relativeCoordinateList.includes("svg.arrowHeadLeft.x1");
			const isArrowHeadLeftY1Relative = relative || relativeCoordinateList.includes("svg.arrowHeadLeft.y1");
			const isArrowHeadLeftX2Relative = relative || relativeCoordinateList.includes("svg.arrowHeadLeft.x2");
			const isArrowHeadLeftY2Relative = relative || relativeCoordinateList.includes("svg.arrowHeadLeft.y2");

			if(isArrowHeadLeftX1Relative) arrowHeadLeft.x1 *= scaleX;
//			else if(invertX) arrowHeadLeft.x1 = screenWidth - arrowHeadLeft.x1;
			if(isArrowHeadLeftY1Relative) arrowHeadLeft.y1 *= scaleY;
//			else if(invertY) arrowHeadLeft.y1 = screenHeight - arrowHeadLeft.y1;
			if(isArrowHeadLeftX2Relative) arrowHeadLeft.x2 *= scaleX;
//			else if(invertX) arrowHeadLeft.x2 = screenWidth - arrowHeadLeft.x2;
			if(isArrowHeadLeftY2Relative) arrowHeadLeft.y2 *= scaleY;
//			else if(invertY) arrowHeadLeft.y2 = screenHeight - arrowHeadLeft.y2;

			const isArrowHeadRightX1Relative = relative || relativeCoordinateList.includes("svg.arrowHeadRight.x1");
			const isArrowHeadRightY1Relative = relative || relativeCoordinateList.includes("svg.arrowHeadRight.y1");
			const isArrowHeadRightX2Relative = relative || relativeCoordinateList.includes("svg.arrowHeadRight.x2");
			const isArrowHeadRightY2Relative = relative || relativeCoordinateList.includes("svg.arrowHeadRight.y2");

			if(isArrowHeadRightX1Relative) arrowHeadRight.x1 *= scaleX;
//			else if(invertX) arrowHeadRight.x1 = screenWidth - arrowHeadRight.x1;
			if(isArrowHeadRightY1Relative) arrowHeadRight.y1 *= scaleY;
//			else if(invertY) arrowHeadRight.y1 = screenHeight - arrowHeadRight.y1;
			if(isArrowHeadRightX2Relative) arrowHeadRight.x2 *= scaleX;
//			else if(invertX) arrowHeadRight.x2 = screenWidth - arrowHeadRight.x2;
			if(isArrowHeadRightY2Relative) arrowHeadRight.y2 *= scaleY;
//			else if(invertY) arrowHeadRight.y2 = screenHeight - arrowHeadRight.y2;

			if(relative || relativeCoordinateList.includes("svg.strokeWidth")) strokeWidth *= scaleX;

			overlay =
			{
				...overlay,
				metadata:
				{
					...overlay.metadata,
					svg:
					{
						...overlay.metadata.svg,
						arrowBody: arrowBody,
						arrowHeadLeft: arrowHeadLeft,
						arrowHeadRight: arrowHeadRight,
						strokeWidth: strokeWidth
					}
				}
			};
		}

		// Handle animation of the overlay
		if(hasValidAnimation(overlay))
		{
			const animation = overlay.metadata.animation;
			let newAnimation = animation;
			if(typeof animation.positions !== "undefined")
			{
				let positions = animation.positions;
				for(let j = 0; j < animation.positions.length; ++j)
				{
					const isXRelative = relative || relativeCoordinateList.includes("animation.position.x");
					const isYRelative = relative || relativeCoordinateList.includes("animation.position.y");
					let animationPositionX = animation.positions[j].x;
					let animationPositionY = animation.positions[j].y;

					if(isXRelative) animationPositionX *= scaleX;
					else if(invertX) animationPositionX = screenWidth - animationPositionX;
					if(isYRelative) animationPositionY *= scaleY;
					else if(invertY) animationPositionY = screenHeight - animationPositionY;

					positions[j] =
					{
						x: animationPositionX,
						y: animationPositionY
					};
				}
				newAnimation =
				{
					...newAnimation,
					positions: positions
				};
			}

			overlays[i] =
			{
				...overlay,
				metadata:
				{
					...overlay.metadata,
					animation: newAnimation,
					geometry: geometry
				}
			};
			continue;
		}

		// NOTE: Using scale on X axis for best look possible. * FontSize renders differently on simulator and device
		if(hasValidText(overlay))
		{
			const text = overlay.metadata.text;
			let fontSize = text.fontSize;
			if(overlay.metadata.geometry.type == "ellipse")
			{
				if(relative || relativeCoordinateList.includes("geometry.radius.x")) fontSize *= scaleX;
			}
			else if(overlay.metadata.geometry.type == "rectangle" || overlay.metadata.geometry.type == "arrow")
			{
				if(relative || relativeCoordinateList.includes("geometry.width")) fontSize *= scaleX;
			}
			else if(overlay.metadata.geometry.type == "floodfill")
			{
			}

			overlays[i] =
			{
				...overlay,
				metadata:
				{
					...overlay.metadata,
					geometry: geometry,
					text:
					{
						...text,
						fontSize: fontSize
					}
				}
			};
			continue;
		}

		overlays[i] =
		{
			...overlay,
			metadata:
			{
				...overlay.metadata,
				geometry: geometry
			}
		};
	}

	// Handle interactions
	let interactions = visionData.interactions;
	for(let i = 0; i < visionData.interactions.length; ++i)
	{
		const interaction = visionData.interactions[i];
		if(!hasValidMetadata(interaction)) continue;
		if(!hasValidAction(interaction)) continue;
		if(!hasValidGeometry(interaction)) continue;

		const invertX = typeof interaction.metadata.invert !== "undefined" && (interaction.metadata.invert == "x" ||
			interaction.metadata.invert == "xy" || interaction.metadata.invert == "yx");
		const invertY = typeof interaction.metadata.invert !== "undefined" && (interaction.metadata.invert == "y" ||
			interaction.metadata.invert == "xy" || interaction.metadata.invert == "yx");
		const invertW = typeof interaction.metadata.invertSize !== "undefined" &&
			(interaction.metadata.invertSize == "w" || interaction.metadata.invertSize == "wh" ||
			interaction.metadata.invertSize == "hw");
		const invertH = typeof interaction.metadata.invertSize !== "undefined" &&
			(interaction.metadata.invertSize == "h" || interaction.metadata.invertSize == "wh" ||
			interaction.metadata.invertSize == "hw");
		const relative = typeof interaction.metadata.relativeCoordinateSystem !== "undefined";
		const relativeCoordinateList = typeof interaction.metadata.mixedCoordinateSystem !== "undefined" ?
			interaction.metadata.mixedCoordinateSystem : [];

		const geometry = recomputeGeometry(interaction, screenWidth, screenHeight, relative, relativeCoordinateList,
			scaleX, scaleY, invertX, invertY, invertW, invertH);

		// NOTE: Using scale on X axis for best look possible. * FontSize renders differently on simulator and device
		if(hasValidText(interaction))
		{
			const text = interaction.metadata.text;
			let fontSize = text.fontSize;
			if(interaction.metadata.geometry.type == "ellipse")
			{
				if(relative || relativeCoordinateList.includes("geometry.radius.x")) fontSize *= scaleX;
			}
			else if(interaction.metadata.geometry.type == "rectangle" || interaction.metadata.geometry.type == "arrow"
				|| interaction.metadata.geometry.type == "triangle" || interaction.metadata.geometry.type == "hexagon"
				|| interaction.metadata.geometry.type == "speech_bubble_ellipse_left" ||
				interaction.metadata.geometry.type == "speech_bubble_rectangle_left")
			{
				if(relative || relativeCoordinateList.includes("geometry.width")) fontSize *= scaleX;
			}
			else if(interaction.metadata.geometry.type == "floodfill")
			{
			}

			interactions[i] =
			{
				...interaction,
				metadata:
				{
					...interaction.metadata,
					geometry: geometry,
					text:
					{
						...text,
						fontSize: fontSize
					}
				}
			};
			continue;
		}

		interactions[i] =
		{
			...interaction,
			metadata:
			{
				...interaction.metadata,
				geometry: geometry
			}
		};
	}

	// Putting things together
	const result =
	{
		...visionData,
		nodes: visionData.nodes,
		media: media,
		overlays: overlays,
		interactions: interactions
	};

	return result;
};

