const DEFAULT_RESIZEMODE = "cover";

class Player extends React.Component
{
	constructor(props)
	{
		super(props);
		this.state = {
			index: 0,
			muted: false,
			autoPlay: false,
			effects_prefix: null
		};
		this.stackIndex = [];
	}

	gotoNode(nodeIndex)
	{
		this.stackIndex.push(this.state.index);
		this.setState({index: nodeIndex});
	}

	triggerEffects(effects_prefix)
	{
		this.setState({effects_prefix: effects_prefix});
		// This is for code-splitting. However, this requires a new index.html, and need to replace every single file per post for S3 server.
//		if(this.state.effects_prefix == "confetti")
//		{
//			import("./confetti.json").then(effects =>
//			{
//				this.setState(
//				{
//					effects: effects,
//					effects_prefix : effects_prefix
//				});
//			});
//		}
//		else if(this.state.effects_prefix == "exploding_confetti")
//		{
//			import("./exploding_confetti.json").then(effects =>
//			{
//				this.setState(
//				{
//					effects: effects,
//					effects_prefix : effects_prefix
//				});
//			});
//		}
	}

	handleInteraction(interaction, nodeIndex)
	{
		if(!interaction.type.includes("hotspot") && !interaction.type.includes("swipe")) return;
		const action = interaction.metadata.action;
		if(action.type == "link")
		{
			this.gotoNode(nodeIndex);
			return;
		}
		else if(action.type == "effects")
		{
			this.triggerEffects(action.prefix);
			return;
		}
		else if(action.type == "url")
		{

		}
	}

	onLottieAnimationEnd = () =>
	{
		this.setState({effects_prefix: null});
	}

	onPressBack()
	{
		const index = this.stackIndex.length == 0 ? 0 : this.stackIndex.pop();
		this.setState({index: index});
	}

	onPressToggleMute()
	{
		this.setState((state) =>
		{
			return {
				muted: !state.muted
			};
		});
	}

	onPressAutoPlay()
	{
		let videos = document.getElementsByTagName("video");
		for(let i = 0; i < videos.length; ++i)
		{
			let video = videos[i];
			if(this.state.autoPlay) video.play();
		}
		this.setState({autoPlay: true});
	}

	renderIcon(type, name, color, size)
	{
		return (
			<Icon font={type} name={name} color={color} size={size}/>
		);
	}

	renderText(text, alignment, color, fontFamily, fontSize)
	{
		if(fontSize == 0) fontSize = 15;
		color = color == "" ? "black" : color;
		let style = {color: color, fontSize: fontSize * FONT_SIZE_MULTIPLIER};
		if(alignment) style = {...style, textAlign: alignment};
		if(fontFamily)
		{
			style = {...style, fontFamily: fontFamily};
			return (
				<div style={style}>
					{text}
				</div>
			);
		}
		return (
			<div style={style}>
				{text}
			</div>
		);
	}

	renderOverlays(node)
	{
		if(typeof node.metadata.overlayIndices == "undefined") return null;
		
		const overlays = node.metadata.overlayIndices.map((index, key) =>
		{
			const overlay = this.props.visionData.overlays[index];
			let x = overlay.metadata.geometry.position.x;
			let y = overlay.metadata.geometry.position.y;
			const width = overlay.metadata.geometry.width;
			const height = overlay.metadata.geometry.height;

			const hasIcon = hasValidIcon(overlay);
			const hasText = hasValidText(overlay);
			const hasSvg = hasValidSvg(overlay);
			const hasView = hasValidView(overlay);

			const hasOpacity = typeof overlay.metadata.opacity != "undefined";
			let opacity = hasOpacity ? overlay.metadata.opacity : DEFAULT_OVERLAY_OPACITY;
			const hasResizeMode = typeof overlay.metadata.resizeMode != "undefined";
			let resizeMode = hasResizeMode ? overlay.metadata.resizeMode : DEFAULT_RESIZEMODE;
		
			if(hasValidMetadata(overlay) && hasValidGeometry(overlay) && overlay.metadata.geometry.type == "ellipse")
			{
				x = overlay.metadata.geometry.position.x - overlay.metadata.geometry.radius.x;
				y = overlay.metadata.geometry.position.y - overlay.metadata.geometry.radius.y;
			}

			if(hasValidMediaIndex(overlay))
			{
				// TODO: Assume that this is a picture... for now
				const media = this.props.visionData.media[overlay.metadata.mediaIndex];
				const uri = media.uri;
				if(!hasOpacity) opacity = typeof media.opacity == "undefined" ? DEFAULT_OVERLAY_OPACITY : media.opacity;
				if(!hasResizeMode) resizeMode = typeof media.resizeMode == "undefined" ? DEFAULT_RESIZEMODE : media.resizeMode;

				//if(resizeMode == "cover") {
				//	return (
				//		<img key={key} src={uri} style={{...styles.absolutePositionContainer, left: x, top: y, width: width, height: height, opacity: opacity,
				//			objectFit: resizeMode}}/>
				//	);
				//}

				resizeMode="contain";

				return (
					<img key={key} src={uri} style={{...styles.absolutePositionContainer, left: x, top: y, width: width, height: height, opacity: opacity,
						objectFit: resizeMode}}/>
				);
			}

			if(hasIcon)
			{
				const icon = overlay.metadata.icon;
				const iconSize = Math.min(overlay.metadata.width, overlay.metadata.height);
				return (
					<div key={key} style={{...styles.absolutePositionContainer, ...overlay.metadata.style, left: x, top: y, width: width, height: height, display: "flex"}} opacity={opacity}>
						{this.renderIcon(icon.type, icon.name, icon.color, iconSize)}
					</div>
				);
			}

			if(hasText)
			{
				const text = overlay.metadata.text;
				const hasTextAlignment = typeof text.alignment !== "undefined";
				const hasFontFamily = typeof text.fontFamily !== "undefined";
				return (
					<div key={key} style={{...styles.absolutePositionContainer, ...overlay.metadata.style, left: x, top: y, width: width, height: height, display: "flex"}} opacity={opacity}>
						{this.renderText(text.text, hasTextAlignment ? text.alignment : null, text.color, hasFontFamily ? text.fontFamily : null, text.fontSize)}
					</div>
				);
			}


			if(hasView)
			{
				const hasBackgroundColor = typeof overlay.metadata.style !== "undefined" &&
					typeof overlay.metadata.style.backgroundColor !== "undefined";
				const hasColor = typeof overlay.metadata.view.color !== "undefined";
				const color = hasColor ? overlay.metadata.view.color : (hasBackgroundColor ?
					overlay.metadata.style.backgroundColor : null);

				if(overlay.metadata.geometry.type == "ellipse") // More like a capsule/round rectangle
				{
					const radiusX = overlay.metadata.geometry.radius.x;
					const radiusY = overlay.metadata.geometry.radius.y;
					const borderRadius = Math.min(radiusX, radiusY);
					const sizeX = radiusX * 2;
					const sizeY = radiusY * 2;

					return (
						<div key={key} style={{...styles.absolutePositionContainer, ...overlay.metadata.style, left: x, top: y, width: sizeX, height: sizeY,
							backgroundColor: color, borderRadius: borderRadius}} opacity={opacity}></div>
					);
				}

				return (
					<div key={key} style={{...styles.absolutePositionContainer, ...overlay.metadata.style, left: x, top: y, width: width, height: height,
						backgroundColor: color}} opacity={opacity}></div>
				);
			}

			return null;
		});

		return overlays;
	}


	renderHotspotIcon(icon, containerWidth, containerHeight)
	{
		let iconSize = Math.min(containerWidth, containerHeight);
		if(typeof icon.scale !== "undefined") iconSize *= icon.scale;
		if(typeof icon.showContainer !== "undefined" && icon.showContainer == "true")
		{
			// NOTE: Being very lenient
			if(typeof icon.innerColor == "undefined")
			{
				return (
					<div style={{alignItems: "center", justifyContent: "center", backgroundColor: HOTSPOT.CONTAINER_COLOR, borderRadius: HOTSPOT_RECTANGLE_BORDER_RADIUS,
						width: containerWidth, height: containerHeight, display: "flex"}}>
						{this.renderIcon(icon.type, icon.name, HOTSPOT.INNER_COLOR, iconSize)}
					</div>
				);
			}
			return (
				<div style={{alignItems: "center", justifyContent: "center", backgroundColor: icon.color, borderRadius: HOTSPOT_RECTANGLE_BORDER_RADIUS,
					width: containerWidth, height: containerHeight, display: "flex"}}>
					{this.renderIcon(icon.type, icon.name, icon.innerColor, iconSize)}
				</div>
			);
		}

		return (
			<div style={{alignItems: "center", justifyContent: "center", width: containerWidth, height: containerHeight, display: "flex"}}>
				{this.renderIcon(icon.type, icon.name, icon.color, iconSize)}
			</div>
		);
	}

	renderHotspotText(text)
	{
		const textAlignment = typeof text.alignment !== "undefined" ? text.alignment : null;
		const fontFamily = typeof text.fontFamily !== "undefined" ? text.fontFamily : null;
		if(typeof text.showContainer !== "undefined" && text.showContainer == "true")
		{
			// NOTE: Being very lenient
			if(typeof text.innerColor == "undefined")
			{
				return (
					<div style={{alignItems: "center", backgroundColor: "white", borderRadius: HOTSPOT_RECTANGLE_BORDER_RADIUS, display: "flex", justifyContent: "center",
						width: "100%", height: "100%"}}>
						{this.renderText(text.text, textAlignment, HOTSPOT.INNER_COLOR, fontFamily, text.fontSize)}
					</div>
				);
			}
			return (
				<div style={{alignItems: "center", backgroundColor: text.color, borderRadius: HOTSPOT_RECTANGLE_BORDER_RADIUS, display: "flex", justifyContent: "center",
					width: "100%", height: "100%"}}>
					{this.renderText(text.text, textAlignment, text.innerColor, fontFamily, text.fontSize)}
				</div>
			);
		}
		if(typeof text.innerColor !== "undefined")
		{
			return this.renderText(text.text, textAlignment, text.innerColor, fontFamily, text.fontSize);
		}

		return this.renderText(text.text, textAlignment, text.color, fontFamily, text.fontSize);
	}

	renderHotspots()
	{
		const node = this.props.visionData.nodes[this.state.index];
		const hotspots = node.metadata.interactionIndices.map((item, index) =>
		{
			const nodeIndex = node.nodeIndices[index];
			const interaction = this.props.visionData.interactions[item];

			const hasColor = typeof interaction.metadata.color !== "undefined";
			const hasImage = hasValidMediaIndex(interaction);
			const hasIcon = hasValidIcon(interaction);
			const hasText = hasValidText(interaction);
		
			let hotspotColor = hasColor ? interaction.metadata.color : "white";
			let renderHotspot = !hasImage && !hasIcon && !hasText;
		
			//if(hasIcon && typeof interaction.metadata.icon.showContainer !== "undefined" && interaction.metadata.icon.showContainer == "false") renderHotspot = true;
			//if(hasText && typeof interaction.metadata.text.showContainer !== "undefined" && interaction.metadata.text.showContainer == "false") renderHotspot = true;
	
			// No buttonified containers
			if(hasIcon)
			{
				if(typeof interaction.metadata.icon.showContainer !== "undefined" &&
					interaction.metadata.icon.showContainer == "false")
				{
					renderHotspot = true;
					if(typeof interaction.metadata.icon.color !== "undefined")
						hotspotColor = interaction.metadata.icon.color;
				}
			}
			if(hasText)
			{
				if(typeof interaction.metadata.text.showContainer !== "undefined" &&
					interaction.metadata.text.showContainer == "false")
				{
					renderHotspot = true;
					if(typeof interaction.metadata.text.color !== "undefined")
						hotspotColor = interaction.metadata.text.color;
				}
			}

			const opacity = typeof interaction.metadata.opacity !== "undefined" ? interaction.metadata.opacity : DEFAULT_HOTSPOT_OPACITY;
			const innerOpacity = typeof interaction.metadata.innerOpacity !== "undefined" ? interaction.metadata.innerOpacity : DEFAULT_HOTSPOT_CONTENT_OPACITY;
			let opacity1 = opacity;
			let opacity2 = innerOpacity;

			let svgStyles = {...styles.absolutePositionContainer};
			let iconTextStyles = {...styles.absolutePositionContainer, ...interaction.metadata.style, display: "flex"};

			if(!interaction.metadata.geometry) return null;
			////////////////////////////////////////////////////
			// TODO: This is a temporary hack... figure out a better way to properly swipe things around using react-use-gesture / react-spring
			if(interaction.type.includes("swipe"))
			{
				const isLeftSwipe = interaction.type == "swipe.left";
				const x = interaction.metadata.geometry.position.x + isLeftSwipe ? 0 : SCREEN_WIDTH / 2;
				const y = interaction.metadata.geometry.position.y;
				const width = interaction.metadata.geometry.width / 2;
				const height = interaction.metadata.geometry.height;

				return (
					<div style={styles.absolutePositionContainer} key={index}>
						{
							renderHotspot &&
								<div style={{...svgStyles, backgroundColor: hotspotColor, borderRadius: 5, left: x, top: y, width: width, height: height, opacity: opacity1}}>
								</div>
						}
						<div style={{...iconTextStyles, left: x, top: y, width: width, height: height, opacity: opacity2}} onClick={() => this.handleInteraction(interaction, nodeIndex)}>
							{hasIcon && this.renderHotspotIcon({...interaction.metadata.icon, name: isLeftSwipe ? "arrow-left-bold" : "arrow-right-bold"}, width, height)}
						</div>
					</div>
				);
			}
			////////////////////////////////////////////////////
			
			if(interaction.metadata.geometry.type == "ellipse")
			{
				const radiusX = interaction.metadata.geometry.radius.x;
				const radiusY = interaction.metadata.geometry.radius.y;
				const sizeX = radiusX * 2 + 1; // NOTE: Added this to avoid ellipsoid's bottom getting cut off
				const sizeY = radiusY * 2 + 1; // NOTE: Added this to avoid ellipsoid's bottom getting cut off
				const x = interaction.metadata.geometry.position.x - radiusX;
				const y = interaction.metadata.geometry.position.y - radiusY;
				return (
					<div style={styles.absolutePositionContainer} key={index}>
						{
							renderHotspot &&
								<div style={{position: "absolute", backgroundColor: hotspotColor, borderRadius: "50%", left: x, top: y, width: sizeX, height: sizeY,
									opacity: opacity1}}>
								</div>
						}
						<div style={{...iconTextStyles, left: x, top: y, width: sizeX, height: sizeY, opacity: opacity2}}
							onClick={() => this.handleInteraction(interaction, nodeIndex)}>
							{hasImage && this.renderHotspotImage(interaction.metadata.mediaIndex)}
							{hasIcon && this.renderHotspotIcon(interaction.metadata.icon, sizeX, sizeY)}
							{hasText && this.renderHotspotText(interaction.metadata.text)}
						</div>
					</div>
				);
			}
			else if(interaction.metadata.geometry.type == "rectangle")
			{
				const x = interaction.metadata.geometry.position.x;
				const y = interaction.metadata.geometry.position.y;
				const width = interaction.metadata.geometry.width;
				const height = interaction.metadata.geometry.height;

				return (
					<div style={styles.absolutePositionContainer} key={index}>
						{
							renderHotspot &&
								<div style={{...svgStyles, backgroundColor: hotspotColor, borderRadius: 5, left: x, top: y, width: width, height: height, opacity: opacity1}}>
								</div>
						}
						<div style={{...iconTextStyles, left: x, top: y, width: width, height: height, opacity: opacity2}} onClick={() => this.handleInteraction(interaction, nodeIndex)}>
							{hasImage && this.renderHotspotImage(interaction.metadata.mediaIndex)}
							{hasIcon && this.renderHotspotIcon(interaction.metadata.icon, width, height)}
							{hasText && this.renderHotspotText(interaction.metadata.text)}
						</div>
					</div>
				);
			}
			else if(interaction.metadata.geometry.type == "triangle")
			{
				const x = interaction.metadata.geometry.position.x;
				const y = interaction.metadata.geometry.position.y;
				const width = interaction.metadata.geometry.width;
				const height = interaction.metadata.geometry.height;
				const x1 = 0;
				const y1 = 0;
				const x2 = width / 2;
				const y2 = height;
				const x3 = width;
				const y3 = 0;

				return (
					<div style={styles.absolutePositionContainer} key={index}>
						{
							renderHotspot &&
								<svg style={{...svgStyles, left: x, top: y, width: width, height: height, opacity: opacity1}} viewBox={"0 0 " + width + " " + height}>
									<polygon style={{x: 0, y: 0, width: width, height: height}} points={x1 + "," + y1 + " " + x2
										+ "," + y2 + " " + x3 + "," + y3} fill={hotspotColor}/>
								</svg>
						}
						<div style={{...iconTextStyles, left: x, top: y, width: width, height: height, opacity: opacity2}} onClick={() => this.handleInteraction(interaction, nodeIndex)}>
							{hasImage && this.renderHotspotImage(interaction.metadata.mediaIndex)}
							{hasIcon && this.renderHotspotIcon(interaction.metadata.icon, width, height)}
							{hasText && this.renderHotspotText(interaction.metadata.text)}
						</div>
					</div>
				);
			}
			else if(interaction.metadata.geometry.type == "hexagon")
			{
				const x = interaction.metadata.geometry.position.x;
				const y = interaction.metadata.geometry.position.y;
				const width = interaction.metadata.geometry.width;
				const height = interaction.metadata.geometry.height;
				const x1 = width * 0.5;
				const y1 = height;
				const x2 = width * (Math.sqrt(3) / 2);
				const y2 = height * (3 / 4);
				const x3 = width * (Math.sqrt(3) / 2);
				const y3 = height * (1 / 4);
				const x4 = width * 0.5;
				const y4 = 0;
				const x5 = width * ((2 - Math.sqrt(3)) / 2);
				const y5 = height * (1 / 4);
				const x6 = width * ((2 - Math.sqrt(3)) / 2);
				const y6 = height * (3 / 4);

				return (
					<div style={styles.absolutePositionContainer} key={index}>
						{
							renderHotspot &&
								<svg style={{...svgStyles, left: x, top: y, width: width, height: height, opacity: opacity1}} viewBox={"0 0 " + width + " " + height}>
									<polygon style={{x: 0, y: 0, width: width, height: height}} points={x1 + "," + y1 + " " + x2
										+ "," + y2 + " " + x3 + "," + y3 + " " + x4 + "," + y4 + " " + x5 + "," + y5 +
										" " + x6 + "," + y6} fill={hotspotColor}/>
								</svg>
						}
						<div style={{...iconTextStyles, left: x, top: y, width: width, height: height, opacity: opacity2}} onClick={() => this.handleInteraction(interaction, nodeIndex)}>
							{hasImage && this.renderHotspotImage(interaction.metadata.mediaIndex)}
							{hasIcon && this.renderHotspotIcon(interaction.metadata.icon, width, height)}
							{hasText && this.renderHotspotText(interaction.metadata.text)}
						</div>
					</div>
				);
			}
			else if(interaction.metadata.geometry.type == "speech_bubble_ellipse_left")
			{
				const x = interaction.metadata.geometry.position.x;
				const y = interaction.metadata.geometry.position.y;
				const width = interaction.metadata.geometry.width;
				const height = interaction.metadata.geometry.height;

				return (
					<div style={styles.absolutePositionContainer} key={index}>
						{
							renderHotspot &&
								<svg style={{...svgStyles, left: x, top: y, width: width, height: height, opacity: opacity1}} viewBox={SVG_SHAPE.SPEECH_BUBBLE_ELLIPSE_LEFT.VIEWBOX}>
									<path d={SVG_SHAPE.SPEECH_BUBBLE_ELLIPSE_LEFT.D} fill={hotspotColor}/>
								</svg>
						}
						<div style={{...iconTextStyles, left: x, top: y, width: width, height: height, opacity: opacity2}} onClick={() => this.handleInteraction(interaction, nodeIndex)}>
							{hasImage && this.renderHotspotImage(interaction.metadata.mediaIndex)}
							{hasIcon && this.renderHotspotIcon(interaction.metadata.icon, width, height)}
							{hasText && this.renderHotspotText(interaction.metadata.text)}
						</div>
					</div>
				);
			}
			else if(interaction.metadata.geometry.type == "speech_bubble_rectangle_left")
			{
				const x = interaction.metadata.geometry.position.x;
				const y = interaction.metadata.geometry.position.y;
				const width = interaction.metadata.geometry.width;
				const height = interaction.metadata.geometry.height;

				return (
					<div style={styles.absolutePositionContainer} key={index}>
						{
							renderHotspot &&
								<svg style={{...svgStyles, left: x, top: y, width: width, height: height, opacity: opacity1}} viewBox={SVG_SHAPE.SPEECH_BUBBLE_RECTANGLE_LEFT.VIEWBOX}>
									<path d={SVG_SHAPE.SPEECH_BUBBLE_RECTANGLE_LEFT.D} fill={hotspotColor}/>
								</svg>
						}
						<div style={{...iconTextStyles, left: x, top: y, width: width, height: height, opacity: opacity2}} onClick={() => this.handleInteraction(interaction, nodeIndex)}>
							{hasImage && this.renderHotspotImage(interaction.metadata.mediaIndex)}
							{hasIcon && this.renderHotspotIcon(interaction.metadata.icon, width, height)}
							{hasText && this.renderHotspotText(interaction.metadata.text)}
						</div>
					</div>
				);
			}
			else if(interaction.metadata.geometry.type == "floodfill")
			{
			}

			return (
				<div style={{position: "absolute", left: interaction.metadata.geometry.position.x, top: interaction.metadata.geometry.position.y}}
					onClick={() => this.handleInteraction(interaction, nodeIndex)} key={index.toString()}>
					<p>{item}</p>
				</div>
			);
		});
		return hotspots;
	}

	renderSound(node)
	{
		if(typeof node.metadata.soundIndex == "undefined") return null;
		const media = this.props.visionData.media[node.metadata.soundIndex];
		const repeat = typeof media.repeat !== "undefined" && media.repeat == "yes";
		let audio = null;
		if(this.state.muted)
		{
			audio = (
				<audio key={media.uri} style={styles.sound} width={1} height={1} autoPlay muted playsInline webkit-playsinline playsinline={"true"}>
					<source src={media.uri} type="audio/mpeg"/>
				</audio>
			);
		}
		else
		{
			audio = (
				<audio key={media.uri} style={styles.sound} width={1} height={1} autoPlay playsInline webkit-playsinline playsinline={"true"}>
					<source src={media.uri} type="audio/mpeg"/>
				</audio>
			);
		}
		const options =
		{
			animationData: require("./electric_guitar.json"),
			autoPlay: true,
			loop: true,
			rendererSettings: {}
		};
		return (
			<div style={styles.soundContainer} onClick={() => this.onPressToggleMute()}>
				<Lottie options={options} style={styles.soundLottie}/>
				{audio}
				<div style={styles.soundIconContainer}>
					{this.state.muted && <Icon font="MaterialCommunityIcons" name="cancel" color="black" size={30} style={styles.soundIcon}/>}
				</div>
			</div>
		);
	}

	renderMedia(node)
	{
		const mediaIndex = node.metadata.mediaIndex;
		const media = this.props.visionData.media[mediaIndex];
		const left = typeof media.left == "undefined" ? 0 : media.left;
		const top = typeof media.top == "undefined" ? 0 : media.top;
		const width = typeof media.width == "undefined" ? SCREEN_WIDTH : media.width;
		const height = typeof media.height == "undefined" ? SCREEN_HEIGHT : media.height;
		const resizeMode = typeof media.resizeMode == "undefined" ? DEFAULT_RESIZEMODE : media.resizeMode;
		if(media.type.includes("video"))
		{
			/*
			return (
				<iframe ref={(ref) => this.videoIframe = ref} key={media.uri} style={{...styles.absolutePositionContainer, backgroundColor: "black", left: left, top: top, width: width, height: height, objectFit: resizeMode}} src={media.uri} autoPlay playsInline webkit-playsinline playsinline={"true"} frameborder={"0"}/>
			);
			*/
			return (
				<video id={"video"} ref={(ref) => this.video = ref} key={media.uri} style={{...styles.absolutePositionContainer, backgroundColor: "black", left: left, top: top, width: width, height: height, objectFit: resizeMode}} playsInline webkit-playsinline playsinline={"true"}>
					<source src={media.uri} type="video/mp4"/>
				</video>
			);
			
		}
		if(media.type.includes("image"))
		{
			return (
				<img src={media.uri} style={{...styles.absolutePositionContainer, backgroundColor: "black", left: left, top: top, width: width, height: height,
					objectFit: resizeMode}}/>
			);
		}
		return (
			<div style={{...styles.absolutePositionContainer, backgroundColor: "black", left: left, top: top, width: width, height: height, objectFit: resizeMode}}>
			</div>
		);
	}

	renderBigPlayButton(node)
	{
		const mediaIndex = node.metadata.mediaIndex;
		const media = this.props.visionData.media[mediaIndex];
		if(media.type.includes("video"))
		{
			if(this.state.autoPlay) return null;
			return (
				<div style={{...styles.absolutePositionContainer, backgroundColor: "black", opacity: 0.8, width: SCREEN_WIDTH, height: SCREEN_HEIGHT,
					alignItems: "center", justifyContent: "center"}}>
					<div style={{display: "flex", alignItems: "center", justifyContent: "center"}} onClick={() => this.onPressAutoPlay()}>
						<Icon font="FontAwesome" name="play" color="white" size={100} style={{padding: 20}}/>
					</div>
				</div>
			);
		}
		return null;
	}

	render()
	{
		const node = this.props.visionData.nodes[this.state.index];
		//let effects = null;
		//if(this.state.effects_prefix == "confetti") effects = require("./confetti.json");
		//else if(this.state.effects_prefix == "exploding_confetti") effects = require("./exploding_confetti.json");
		//const effects = this.state.effects;
		let effects = null;
		let effects_width = this.props.width;
		let effects_height = this.props.height;
		if(this.state.effects_prefix == "confetti")
		{
			effects = require("./confetti.json");
			effects_width *= 1;
			effects_height *= 1.2;
		}
		else if(this.state.effects_prefix == "exploding_confetti")
		{
			effects = require("./exploding_confetti.json");
			effects_width *= 1;
			effects_height *= 1;
		}
		else if(this.state.effects_prefix == "correct")
		{
			effects = require("./correct.json");
			effects_width *= 1;
			effects_height *= 1;
		}
		else if(this.state.effects_prefix == "wrong")
		{
			effects = require("./wrong.json");
			effects_width *= 1;
			effects_height *= 1;
		}
		else if(this.state.effects_prefix == "heart")
		{
			effects = require("./heart.json");
			effects_width *= 1;
			effects_height *= 1;
		}
		else if(this.state.effects_prefix == "emoji_heart")
		{
			effects = require("./emoji_heart.json");
			effects_width *= 0.4;
			effects_height *= 0.4;
		}
		else if(this.state.effects_prefix == "emoji_happy")
		{
			effects = require("./emoji_happy.json");
			effects_width *= 0.4;
			effects_height *= 0.4;
		}
		else if(this.state.effects_prefix == "emoji_sad")
		{
			effects = require("./emoji_sad.json");
			effects_width *= 0.4;
			effects_height *= 0.4;
		}
		else if(this.state.effects_prefix == "emoji_confused")
		{
			effects = require("./emoji_confused.json");
			effects_width *= 0.4;
			effects_height *= 0.4;
		}
		else if(this.state.effects_prefix == "emoji_crying")
		{
			effects = require("./emoji_crying.json");
			effects_width *= 0.4;
			effects_height *= 0.4;
		}
		const options =
		{
			animationData: effects,
			autoPlay: true,
			loop: false,
			rendererSettings: {}
		};
		const eventListeners =
		[
			{
				eventName: "complete",
				callback: this.onLottieAnimationEnd
			}
		];
		return (
			<div style={{...styles.absolutePositionContainer, width: SCREEN_WIDTH, height: SCREEN_WIDTH}}>
				{this.renderMedia(node)}
				{this.renderSound(node)}
				{this.renderOverlays(node)}
				{this.renderHotspots()}
				{
					effects &&
						<div style={{...styles.absolutePositionContainer, width: SCREEN_WIDTH, height: SCREEN_HEIGHT, alignItems: "center", justifyContent: "center"}}>
							<Lottie options={options} style={{width: effects_width, height: effects_height}} eventListeners={eventListeners}/>
						</div>
				}
				{
					this.state.index != 0 &&
						<div style={{position: "relative", left: SCREEN_WIDTH - 50, display: "flex", flexDirection: "row", justifyContent: "flex-end", width: 50}}>
							<div style={{display: "flex"}} onClick={() => this.onPressBack()}>
								<Icon font="SimpleLineIcons" name="arrow-left" color="white" size={25} style={{padding: 20}}/>
							</div>
						</div>
				}
				{this.renderBigPlayButton(node)}
			</div>
		);
	}

	componentDidMount()
	{
		let videos = document.getElementsByTagName("video");
		for(let i = 0; i < videos.length; ++i)
		{
			let video = videos[i];
			if(this.state.autoPlay) video.play();
			//videojs("video", {controls: false}, () =>
			//videojs(this.video, {controls: false}, () =>
			//videojs(video, {controls: false}, () =>
			videojs(this.video, {controls: false}, function()
			{
				let adapter = new playerjs.VideoJSAdapter(this);
				adapter.ready();
			});
		}
		
	}
};

